.footer-main {
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
}
.footer-nav-object {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 1.2rem;
  font-weight: 500;
}
.footer-nav-object:hover {
  cursor: pointer;
}
.footer-nav-object:not(:first-child) {
  margin-left: 30px;
}
.footer-nav-active-link {
  font-size: 1.5rem;
}
.footer-right {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 1.2rem;
  font-weight: 500;
}
.footer-social-pictures {
  margin-left: 20px;
  display: flex;
}
.footer-social-logo {
  width: 30px;
}
.footer-social-img {
  width: 100%;
}
@media screen and (max-width: 1160px) {
  .footer-nav-object, .footer-social-text {
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 1000px) {
  .footer-main {
    flex-direction: column;
  }
    .footer-nav-object, .footer-social-text {
    font-size: 0.8rem;
  }
}
%text-footer {
  font-size: 1.2rem;
  font-weight: 500;
}
