.proud-of-poland-container {
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: auto;
}

.proud-poland-picture {
  max-width: 150px;
  margin: auto;
  width: 70%;
}
.proud-poland-picture-main {
  max-width: 250px;
  margin: auto;
  width: 100%;
}
.proud-of-poland {
  width: 23%;
  min-width: 120px;
  position: relative;
  background-color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
.poland-proud-button {
  width: 100%;
  background-color: rgba(19, 46, 113, 1);
  color: white;
  padding: 5px 10px;
  text-align: center;
}
.proud-poland-text {
  width: 90%;
  margin: auto;
  text-align: center;
  padding: 10px;
  font-size: 1rem;
  font-weight: 500;
  background-color: blue;
}
.proud-text-item {
  width: 70%;
}
.proud-of-poland-container {
display: flex;
    justify-content: space-between;
    width: 80%;
    margin: auto;
}

@media screen and (max-width: 1000px) {
 
  .proud-of-poland-container {
    width: 90%;
  }
}
